<template>
  <div id="Home">
    <div>{{pokemon.name}}</div>
    <div class="container">
      <div v-for="(pokemon, index) in resultSearch" :key="index">
        <Card :name="pokemon.name" :url="pokemon.url" :id="index + 1" />
      </div>
    </div>
  </div>
</template>

<script>
import {
  mapState,
} from 'vuex'
import axios from "axios";
import Card from '@/components/Card/Card.vue'
export default {
  name: 'Home',
  components: {
    Card,
  },
  data() {
    return {
      pokemons: [],
      busca: ""
    };
  },
  async created() {
    await axios.get("https://pokeapi.co/api/v2/pokemon?limit=151&offset=0").then((res) => {
      this.pokemons = res.data.results;
    });
  },
  computed: {
    ...mapState(['pokemon']),
    resultSearch() {
      let pokemon = this.pokemon.name;      
      if (pokemon == '' || pokemon == ' ') {             
        return this.pokemons;
      } else {       
        return this.pokemons.filter((pokemon) => pokemon.name == this.pokemon.name);
      }
    }
  },

}
</script>





<style src="./style.scss" lang="scss" scoped />