import { createStore } from 'vuex';

export default createStore({
  state: {
    pokemon: {
      name: ''
    }
  },
  mutations: {
    searchPokemon(state, payload) {
      state.pokemon.name = payload;
    }
  },
  actions: {
    setSearch(context, payload) {
      context.commit('searchPokemon', payload);      
    }
  }
})